* {
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
}

.main {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    gap: 10px;
    padding-left: 60px;
}

.boxs {
    width: 150px;
    background-color: rgb(220, 232, 232);
    height: 150px;
    padding: 0px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.boxs:hover {
    transform: scale(1.1);
    transition: 1.2s;
}

.boxs img {
    width: 110px;
}

.boxs p {
    font-size: 10px;
}

.boxs h3 {
    font-size: 15px;
}

.pteg {
    color: rgb(228, 238, 235);
    background-color: rgb(236, 12, 12);
    text-align: center;
    margin-top: 5px;
}

.h1 {
    text-align: center;
    margin-bottom: 10px;
    color: crimson;
}